<template>
  <q-page nopadding>
    <q-card flat class="bg-white text-black">
      <q-card-section style="margin-top: 120px;" horizontal>
        <instrument-control-fixed v-for="instrument in instruments" :key="instrument.id" :selectedInstrumentName='instrument.name' :selectedInstrumentId=instrument.id :selectedInstrumentHasTuner='instrument.type === "guitar"'></instrument-control-fixed>
      </q-card-section>
    </q-card>
  </q-page>
</template>

<script>

import InstrumentControlFixed  from '@/components/InstrumentControlFixed.vue';
import { getInstruments } from 'src/services/apiServices';

export default {
  components: {
    InstrumentControlFixed
  },
  data() {
    return {
      instruments: [],
      instrumentFilter: [],
    };
  },
  created() {
    this.loadRoutData();
    this.fetchInstruments();
  },
  watch: {
    $route() {
      this.loadRoutData();
      this.fetchInstruments();
    }
  },
  methods: {
    loadRoutData() {
      this.instrumentFilter = this.$route.params.filter.split('-and-')
    },
    fetchInstruments() {
      getInstruments()
        .then(response => {
          this.instruments = response.data.filter(instrument => this.instrumentFilter.includes(instrument.type));
        })
        .catch(error => {
          console.error('Error fetching instruments:', error);
        });
    }
  }
}
</script>